body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* overflow-x: hidden; */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Bai Jamjuree", sans-serif;
}

.hero-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  background-image: url(/clipboard-landing-page/static/media/bg-header-mobile.80627651.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  min-height: 55vh;
}
.hero-wrapper .logo {
  height: 150px;
  width: 300px;
  margin: 75px 0;
}
.hero-wrapper .heading {
  color: hsl(210deg, 10%, 33%);
  font-size: 2.5rem;
  font-weight: 600;
  margin-bottom: 20px;
}
.hero-wrapper .body {
  font-size: 19px;
  color: hsl(201deg, 11%, 66%);
  line-height: 1.5rem;
  margin-bottom: 40px;
}
.hero-wrapper .btns {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 35px;
  gap: 35px;
  width: 80%;
}
.hero-wrapper .btns .green {
  background-color: hsl(171deg, 66%, 44%);
}
.hero-wrapper .btns .blue {
  background-color: hsl(233deg, 100%, 69%);
}

.btn {
  padding: 18px 24px;
  border: none;
  border-radius: 30px;
  width: 100%;
  font-size: 1.2rem;
  color: white;
  font-weight: 300;
  box-shadow: -1px 15px 30px -4px rgba(0, 0, 0, 0.3);
  -webkit-box-shadow: -1px 15px 30px -4px rgba(0, 0, 0, 0.3);
  -moz-box-shadow: -1px 15px 30px -4px rgba(0, 0, 0, 0.3);
}

@media screen and (min-width: 768px) {
  .hero-wrapper {
    max-height: 75vh;
  }
  .btn {
    padding: 12px 20px;
    border-radius: 50px;
    width: 80%;
    font-size: 0.9rem;
  }
  .btns {
    width: 20%;
  }
}
@media screen and (min-width: 1024px) {
  .btn {
    padding: 12px 5px;
    border: none;
    border-radius: 50px;
    max-width: 14%;
    font-size: 0.9rem;
  }
}
.pad {
  padding: 20px 25px;
}

@media (min-width: 768px) {
  .hero-wrapper {
    background-image: url(/clipboard-landing-page/static/media/bg-header-desktop.df6601b0.png);
  }
  .hero-wrapper .logo {
    height: 110px;
    width: 150px;
    margin: 70px 0 50px 0;
  }
  .hero-wrapper .body {
    width: 650px;
    margin-bottom: 45px;
  }
  .hero-wrapper .btns {
    display: flex;
    flex-direction: row;
    justify-content: center;
    grid-gap: 35px;
    gap: 35px;
    width: 700px;
  }
  .hero-wrapper .btn {
    padding: 14px 5px;
    border: none;
    min-width: 10%;
    max-width: 30%;
    font-size: 0.9rem;
  }
}/*# sourceMappingURL=Hero.css.map */
